import { Col } from "react-bootstrap"

export const ProjectCard = ({title, desc, imgUrl}) =>{
    return (
        <Col sm={6} md={4}>
            <div className="proj-imgbx">
                <img src={imgUrl} alt={title} />
                <div className="proj-txtx">
                    <h4>{title}</h4>
                    <span>{desc}</span>
                </div>
            </div>
        </Col>
    )
}